}

/* inside card body of card   */
.Services .container .row .testimotionals {
  display: inline-block;
  margin-left: 50px;
  height: 350px;
}
/* single card */
.container .row .testimotionals .card {
  height: 350px;
  position: relative;
  overflow: hidden;
  width: 350px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  text-align: center;
  box-shadow: 1px 1px 14px rgb(207, 206, 206);
  margin-left: -60px;
}
.container .row .testimotionals .card:hover {
  box-shadow: none;
  border: none;
}
/*  layer on card change background and color of text */
.Services .container .row .testimotionals .card .layer {
  z-index: 1;
  position: absolute;
  top: calc(100% - 0px);
  height: 100%;
  width: 100%;
